import React from "react";
import Project from "./Project";

const Projects = (props) => {
  const { projects } = props;
  return (
    <>
      {" "}
      <h2 className="heading">Projects</h2>
      {projects.map((value, index) => {
        return <Project {...value} />;
      })}
      {/* {JSON.stringify(props)} */}
    </>
  );
};

export default Projects;
